export default {
  name: "keyword",
  data: function () {
    return {
      search_result: undefined,
      keyword: undefined
    }
  },
  created() {
    this.load_search();
  },
  methods: {
    load_search: function () {
      this.keyword = this.$route.params.keyword;
      this.set_title(this.keyword);

      var post_data = {
        keyword: this.keyword,
        start: 0,
        limit: 50
      }
      this.$axios.post('/app/keyword', post_data).then((response) => {
        console.log('search 结果:', response);
        this.search_result = response.data;
        this.on_load(this.search_result);
      }).catch(function (error) {

      });
    },
    on_load: function (search_result) {
      this.set_title(this.keyword);
    }
  },
  watch: {
    '$route'(to, from) {
      this.search_result = undefined;
      this.$nextTick(() => {
        this.load_search();
        this.$forceUpdate();
      });
    }
  },
}
